<template>
    <div class="sidebar">
        <transition name="fade">
            <nav v-if="isPanelOpen" class="sidebar-panel">
                <button v-for="routes in links" v-bind:key="routes.id" class="sidebar-links btn btn-one" @click="redirect(routes.page)">
                  {{ routes.text }}
                </button>
            </nav>
        </transition>
    </div>
</template>
<script>
import { store, mutations } from '../../store.js'

export default {
  methods: {
    closeSidebarPanel: mutations.toggleNav,
    redirect (arg) {
      this.$router.push(arg)
    }
  },
  computed: {
    isPanelOpen () {
      return store.isNavOpen
    }
  },
  data () {
    return {
      links: [
        {
          id: 0,
          text: 'Home',
          page: '/',
          icon: 'fas fa-home'
        },
        {
          id: 1,
          text: 'About',
          page: '/About',
          icon: 'fas fa-address-card'
        },
        {
          id: 2,
          text: 'Projects',
          page: '/projects',
          icon: 'fas fa-project-diagram'
        },
        {
          id: 3,
          text: 'Blog',
          page: '/blog',
          icon: 'fas fa-blog'
        }
      ]
    }
  }
}
</script>
<style>
    .fade-enter-active,
    .fade-leave-active
    {
        transition: opacity 0.5s ease;
    }

    .fade-enter,
    .fade-leave-to {
      opacity: 0
    }

    .sidebar-panel {
        overflow-y: auto;
        overflow-x: hidden;
        position: fixed;
        left: 0;
        top: 0;
        height: 100vh;
        z-index: 1000;
        margin-left: 60px;
        margin-top: 5px;
        display: flex;
    }

    .sidebar-links {
      display: flex;
      align-items: center;
      height: 60px;
      color: #666;
      cursor: pointer;
      font-size: 20px;
      letter-spacing: 2.5px;
      width: 100%;
      margin-right: 10px;
      color: white;
      transition: 0.25s;
    }

  .sidebar-links:hover {
      color: lightblue;
      transform: scale(1.1);
  }

</style>



