<template>
 <div class="project-slider">
      <slider ref="slider" :options="options">
          <slideritem v-for="(item,index) in projectsList" :key="index" :class="item.class" v-html="item.html"></slideritem>
          <div slot="loading">Loading...</div>
      </slider>
 </div>
</template>

<script>
import { slider, slideritem } from 'vue-concise-slider'

export default {
  components: {
    slider,
    slideritem
  },
  methods: {
    onClick (args) {
      window.open(args, '_blank')
    }
  },
  data () {
    return {
      projectsList: [
        {
          html: '<div><h1>Project A</h1><p>This is a long text string.</p></div>',
          'class': 'project-a',
          'link': 'http://www.google.com'
        },
        {
          html: '<div><h1>Project B</h1><p>Test text!</p></div>',
          'class': 'project-a',
          'link': 'http://www.google.com'
        },
        {
          html: '<div><h1>Project C</h1><p>Test text!</p></div>',
          'class': 'project-a',
          'link': 'http://www.google.com'
        }
      ],
      options: {
        currentPage: 0,
        thresholdDistance: 500,
        thresholdTime: 100,
        autoplay: 5000,
        loop: true,
        direction: 'horizontal',
        loopedSlides: 1,
        slidesToScroll: 1,
        timingFunction: 'ease',
        speed: 1000
      }
    }
  }
}
</script>

<style scoped>
    .project-slider {
        width: 100%;
        height: 90vh;
    }

    .project-a {
      color: black;
      display: block;
    }
</style>



