<template>
  <div class="topbar row">
    <Burger></Burger>
    <div class="header">
      <ul class="header-nav">
          <li v-for="profile in profiles" :key="profile.id">
              <a :href="`${profile.link}`" target="_blank">
                  <i :class="profile.icon"></i>
              </a>
          </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Burger from './Navigation/Burger'

export default {
  components: {
    'Burger': Burger
  },
  data () {
    return {
      profiles: [
        {
          id: 0,
          link: 'https://github.com/azadnalbandian',
          icon: 'header-icon fab fa-github fa-2x'
        },
        {
          id: 1,
          link: 'https://www.linkedin.com/in/azad-nalbandian-057a4b149/',
          icon: 'header-icon fab fa-linkedin fa-2x'
        },
        {
          id: 2,
          link: 'mailto:azadnalbandian@gmail.com',
          icon: 'header-icon fas fa-at fa-2x'
        }
      ]
    }
  },
  computed: {
    currentRouteName () {
      return this.$route.name
    }
  }
}
</script>

<style scoped>
  .topbar {
    position: fixed;
    display: flex;
    top: 0;
    width: 100%;
    height: 70px;
    z-index: 999;
  }

  h1 {
    line-height: 60px;
    margin-left: 60px;
    color: white;
    font-size: 30px;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  }

  .header {
    position: fixed;
    right: 0;
    top: 15px;
  }

  ul.header-nav {
      list-style-type: none;
  }

  ul.header-nav > li {
      display: inline;
  }

  .header-icon {
      color: white;
      transition: 0.25s;
  }

  .header-icon:hover {
      color: lightblue;
      transform: scale(1.5);
  }

</style>



