<template>
    <div class="parent-box row justify-content-center">
        <div class="box-content col-6">
            <ProjectCard></ProjectCard>
        </div>
    </div>
</template>

<script>
import ProjectCard from './Elements/ProjectCard'

export default {
  components: {
    'ProjectCard': ProjectCard
  },
  name: 'Projects',
  data () {
    return {
      projects: [
        {
          id: 0,
          name: 'test'
        },
        {
          id: 1,
          name: 'test2'
        }
      ]
    }
  }
}
</script>

<style scoped>
</style>



